<style>
@import url(../css/Import.css);
</style>
<template>
<div>
  <el-row class="padding-10-0">
    <el-upload
        class="upload-demo float inline-block"
        action=""
        :on-change="handleChange"
        :on-exceed="handleExceed"
        :on-remove="handleRemove"
        :file-list="fileListUpload"
        :limit="limitUpload"
        accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel"
        :auto-upload="false">
        <el-button size="small" type="primary"></el-button>
        <div slot="tip" class="el-upload__tip">    xlsx / xls  </div>
    </el-upload>
    <el-button size="small" class="float" type="success" @click="InsertDataTable"></el-button>
    <el-button size="small" class="float" type="success" @click="GetDataTable"></el-button>
    <el-button size="small" class="float" type="danger" @click="OutTable">Excel</el-button>
    <el-button size="small" class="float" type="info">:{{this.NOWTableType}}</el-button>
  </el-row>
    <el-table
      :data="tableData"
      id="out-table"
      @cell-click="open"
      :cell-class-name='getCellIndex'
      style="width: 100%">
      <el-table-column
        prop="datetime"
        max-width="200"
        column-key="date"
        label=""
        :formatter="dateFormat">
      </el-table-column>
      <el-table-column
        prop="shift"
        label=""
        max-width="120">
      </el-table-column>
      <el-table-column
        prop="onduty"
        label=""
        max-width="120">
      </el-table-column>
      <el-table-column
        prop="site"
        label=""
        max-width="200">
      </el-table-column>
      <el-table-column
        prop="shipname"
        label=""
        max-width="200">
      </el-table-column>
      <el-table-column
        prop="consignor"
        label=""
        max-width="200">
      </el-table-column>
      <el-table-column
        prop="jobtype"
        label=""
        max-width="200">
      </el-table-column>
      <el-table-column
        prop="team"
        label=""
        max-width="120">
      </el-table-column>
      <el-table-column
        prop="workload"
        label=""
        max-width="120">
      </el-table-column>
      <el-table-column
        prop="planQty"
        label=""
        max-width="200">
      </el-table-column>
      <el-table-column
        prop="remarks"
        label=""
        max-width="200">
      </el-table-column>
    </el-table>
</div>
</template>
<script>
import moment from 'moment'
// eslint-disable-next-line no-unused-vars
import { Message } from 'element-ui'
// eslint-disable-next-line no-unused-vars
import FileSaver from 'file-saver'
// eslint-disable-next-line no-unused-vars
import XLSX from 'xlsx'

export default {
  inject: ['reload'],
  name: 'Import',
  data () {
    return {
      fileListUpload: [],
      fileTemp: null,
      limitUpload: 1,
      tableList: [],
      tableData: [],
      NOWTableType: ''
    }
  },
  methods: {
    getCellIndex: function ({ row, column, rowIndex, columnIndex }) {
      row.index = rowIndex
      column.index = columnIndex
    },
    handleChange (file, fileList) {
      this.fileTemp = file.raw
      this.tableData = []
      if (this.fileTemp) {
        if ((this.fileTemp.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet') || (this.fileTemp.type === 'application/vnd.ms-excel')) {
          this.importfxx(this.fileTemp)
        } else {
          this.$message({
            type: 'warning',
            message: ''
          })
        }
      } else {
        this.$message({
          type: 'warning',
          message: ''
        })
      }
    },
    handleRemove (file, fileList) {
      this.fileTemp = null
      this.tableData = []
      this.NOWTableType = ''
    },
    importfxx (obj) {
      // eslint-disable-next-line no-unused-vars
      let _this = this
      // DOM
      this.file = obj
      var rABS = false // 
      var f = this.file
      var reader = new FileReader()
      // if (!FileReader.prototype.readAsBinaryString) {
      FileReader.prototype.readAsBinaryString = function (f) {
        var binary = ''
        var rABS = false // 
        // eslint-disable-next-line no-unused-vars
        var pt = this
        var wb // 
        var outdata
        var reader = new FileReader()
        reader.onload = function (e) {
          var bytes = new Uint8Array(reader.result)
          var length = bytes.byteLength
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i])
          }
          var XLSX = require('xlsx')
          if (rABS) {
            // eslint-disable-next-line no-undef
            wb = XLSX.read(btoa(fixdata(binary)), { // 
              type: 'base64'
            })
          } else {
            wb = XLSX.read(binary, {
              type: 'binary'
            })
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]])// outdata
          //   console.log(JSON.stringify(outdata))
          this.da = [...outdata]
          var zuoye = ''
          this.da.map(v => {
            // eslint-disable-next-line no-unused-vars
            var a = v['__EMPTY']
            if (a) {
              if (a.indexOf('') !== -1) {
                zuoye = v['__EMPTY']
              }
            }
            if (v['__EMPTY_1'] && v['__EMPTY_1'] !== '' && v['__EMPTY_1'] !== '' && v['__EMPTY_1'] !== '') {
              let obj = {
                'datetime': new Date().getFullYear() + '/' + (new Date().getMonth() + 1) + '/' + (new Date().getDate() - 1),
                'site': v['__EMPTY_1'],
                'shipname': v['__EMPTY_2'] ? v['__EMPTY_2'] : '',
                'consignor': v['__EMPTY_4'] ? v['__EMPTY_4'] : '',
                'jobtype': v['__EMPTY_3'] + zuoye,
                'shift': new Date().getFullYear() + '/' + (new Date().getMonth() + 1) + '/' + (new Date().getDate() - 1) + '',
                'onduty': '',
                'team': '',
                'workload': 0,
                'planQty': 0,
                'remarks': ''
              }
              _this.tableData.push(obj)
            }
            // console.log(JSON.stringify(v))
          })
        }
        reader.readAsArrayBuffer(f)
        _this.NOWTableType = ''
      }

      if (rABS) {
        reader.readAsArrayBuffer(f)
      } else {
        reader.readAsBinaryString(f)
      }
    },
    handleExceed (file, fileList) {
      this.$message({
        type: 'warning',
        message: 'Excel'
      })
    },
    open (row, column, event, cell) {
      // eslint-disable-next-line no-unused-vars
      var Data = this.tableData[row.index][column.property]
      console.log(column)
      // console.log(this.tableData[row.index][column.property])
      // console.log(row[column['property']])
      // console.log(JSON.stringify(column))
      // console.log(JSON.stringify(cell))
      // console.log(JSON.stringify(event))
      this.$prompt(column.label, {
        confirmButtonText: '',
        cancelButtonText: '',
        inputValue: this.tableData[row.index][column.property]
      }).then(({ value }) => {
        this.tableData[row.index][column.property] = value
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    dateFormat (row, column) {
      const date = new Date(row[column.property])
      if (date === undefined) { return '' }
      return moment(date).format('YYYY-MM-DD')
    },
    InsertDataTable () {
      var that = this
      // console.log(JSON.stringify(that.tableData))
      if (that.tableData.length !== 0) {
        that.$.ajax({
          type: 'POST',
          url: that.api.baseURL + 'Import/Insert',
          contentType: 'application/json; charset=utf-8',
          data: JSON.stringify(that.tableData),
          dataType: 'JSON',
          success: function (response) {
            if (response.result === '') {
              that.$message({
                type: 'success',
                message: response.result
              })
              that.reload()
            } else {
              that.$message({
                type: 'error',
                message: response.result
              })
            }
          }
        })
      } else {
        that.$message({
          type: 'warning',
          message: ''
        })
      }
    },
    GetDataTable () {
      this.tableData = null
      this.fileListUpload = []
      var that = this
      that.$.ajax({
        type: 'GET',
        url: that.api.baseURL + 'Import/GetAll',
        contentType: 'application/json; charset=utf-8',
        async: true,
        success: function (response) {
          that.tableData = response.result.items
          that.NOWTableType = ''
        }
      })
    },
    OutTable () {
      /*  */
      var wb = XLSX.utils.table_to_book(document.querySelector('#out-table'))
      /*  */
      var wbout = XLSX.write(wb, {
        bookType: 'xlsx',
        bookSST: true,
        type: 'array'
      })
      try {
        FileSaver.saveAs(
          // Blob 
          // Blob JavaScript
          // File Blob blob 
          //  Blob 
          new Blob([wbout], { type: 'application/octet-stream' }),
          // 
          (new Date().getMonth() + 1) + '.xlsx'
        )
      } catch (e) {
        if (typeof console !== 'undefined') console.log(e, wbout)
      }
      return wbout
    }
  },
  mounted () {
  }
}
</script>



